@charset "utf-8";

/* ------------------------------------------------------------
   1) Sass変数（フォントスタック）
   - ここはプロジェクト共通の"名前付きフォント"の定義だけ。
   - 読み込み（@font-face / Google Fonts）は別ファイル・<head>で。
   - 変数名は「何のフォントか」が一目で分かるように明確化する。
------------------------------------------------------------ */

/* Google Fonts / Web Fonts 系（読み込み側の font-family 名と一致させる） */
$shipporiMincho: "Shippori Mincho", serif;
$unna: "Unna", serif;

/* 日本語環境のシステムフォント（フォールバック用）
   - ヒラギノは端末依存で「和名/英名の揺れ」が出やすい
   - なので英名を先に並べ、和名は後ろに回して当たりやすくする
*/
$hiraginoGothic: "Hiragino Kaku Gothic ProN", "Hiragino Kaku Gothic Pro", "Hiragino Sans", "ヒラギノ角ゴシック", "メイリオ", Meiryo, "ＭＳ Ｐゴシック", "MS PGothic", Arial, sans-serif;

/* 游ゴシック */
$yuGothic: "Yu Gothic", "YuGothic", "游ゴシック体", "Hiragino Kaku Gothic ProN", "Hiragino Sans", "メイリオ", Meiryo, Arial, sans-serif;

/* 游明朝 */
$yuMincho: "Yu Mincho", "YuMincho", "游明朝体", "Hiragino Mincho ProN", "Hiragino Mincho Pro", "MS PMincho", serif;

:root {
   /* fonts: ページ側は var(--ff-xxx) で参照する */
   --ff-shipporiMincho: #{$shipporiMincho};
   --ff-unna: #{$unna};
   --ff-hiraginoGothic: #{$hiraginoGothic};
   --ff-yuGothic: #{$yuGothic};
   --ff-yuMincho: #{$yuMincho};

   /* （任意）システムUIフォントの汎用指定
     先に system-ui → Apple/Chrome系 → Win/Android → 最終 sans-serif
     ※UI（ボタン等）に使うと "OSっぽさ" が出て自然になりやすい
  */
   --ff-system: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Noto Sans", "Helvetica Neue", Arial, sans-serif;
}