@charset "UTF-8";
@use '../global' as *;

// ============= hero =============
.p-hero {
  position: relative;
  background: var(--color-off-white);
}

.p-hero__image {
  position: relative;
  height: 100vh;
  opacity: 0;

  @include mq(max, tab) {
    height: 100vh;
  }

  picture {
    display: block;
    width: 100%;
    height: 100%;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &::after {
    content: "";
    position: absolute;
    inset: 0;
    background: linear-gradient(to bottom,
        rgba(108, 103, 94, 0) 48%,
        rgba(108, 103, 94, 0.9) 100%);
  }
}

.p-hero__catch {
  position: absolute;
  left: 7.4rem;
  bottom: 6.8rem;

  @include mq(max, tab) {
    left: 2rem;
    bottom: 4rem;
  }
}

.p-hero__catch-main {
  margin-bottom: 4.1rem;
  font-size: 7.434rem;
  line-height: 1.4;
  letter-spacing: 0.1em;
  color: var(--color-white01);

  @include mq(max, tab) {
    margin-bottom: 2.5rem;
    font-size: 5rem;
    line-height: 1.2;
  }
}

.p-hero__catch-sub {
  font-family: var(--ff-unna);
  font-size: 3.053rem;
  line-height: 1.2;
  letter-spacing: 0.1em;
  color: var(--color-white01);
  opacity: 0;

  @include mq(max, tab) {
    font-size: 2.5rem;
  }
}

// ============= intro =============
.p-concept {
  padding-top: 16rem;
  background: var(--color-off-white);

  @include mq(max, tab) {
    padding-top: 8rem;
  }
}

.p-concept__container {
  display: flex;
  gap: 0 8rem;
  margin-bottom: 12rem;

  @include mq(max, tab) {
    flex-direction: column;
    gap: 5rem 0;
    margin-bottom: 8rem;
  }
}

.p-concept__title {
  opacity: 0;
  font-size: 5rem;
  line-height: 1.6;
  letter-spacing: 0.1em;
  white-space: nowrap;

  @include mq(max, tab) {
    font-size: 4rem;
  }
}

.p-concept__text {
  opacity: 0;
  font-size: 1.8rem;
  line-height: 2.3;
  letter-spacing: 0.05em;
  color: var(--color-black01);

  @include mq(max, tab) {
    font-size: 1.6rem;
    line-height: 2;
  }

  p+p {
    margin-top: 5rem;

    @include mq(max, tab) {
      margin-top: 3rem;
    }
  }
}

.p-concept__video {
  max-width: 76.8rem;
  margin-inline: auto;
}

.p-concept__video-player {
  position: relative;
  width: 100%;
  aspect-ratio: 768 / 430;
  background: var(--color-light-gray);

  @include mq(max, tab) {
    aspect-ratio: auto;
  }

  video {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.p-concept__video-note {
  margin-top: 2rem;
  font-size: 1.4rem;
  line-height: 1.6;
  letter-spacing: 0.1em;
  text-align: right;

  @include mq(max, tab) {
    font-size: 1.2rem;
  }
}

// ============= value =============
.p-value {
  padding-top: 14rem;
  background: var(--color-off-white);

  .c-title {
    margin-bottom: 7rem;
  }
}

.p-value__item {
  position: relative;
}

.p-value__item-image {
  position: relative;
  height: 65rem;
  overflow: hidden;

  picture {
    position: absolute;
    inset: 0;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.p-value__item-note {
  position: absolute;
  left: 4rem;
  bottom: 2.6rem;
  line-height: 1.6;
  letter-spacing: 0.1em;
  color: var(--color-white01);

  @include mq(max, tab) {
    left: 2rem;
    bottom: 2rem;
    font-size: 1.4rem;
  }
}

.p-value__item-body {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 12.8rem;
  margin-block: auto;
  width: 51.2rem;
  height: 50.1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 5rem;
  background: rgba(255, 255, 255, 0.8);

  .p-value__item:nth-child(even) & {
    right: auto;
    left: 12.8rem;
  }

  @include mq(max, tab) {
    inset: 0;
    margin: auto;
    width: 89.33%;
    height: 50.1rem;
    padding: 0 2.2rem;

    // 偶数番目もSPでは中央
    .p-value__item:nth-child(even) & {
      inset: 0;
    }
  }
}

.p-value__item-title {
  align-self: stretch;
  margin-bottom: 4rem;
  font-size: 4rem;
  line-height: 1.5;
  letter-spacing: 0.1em;

  @include mq(max, tab) {
    font-size: 3rem;
  }
}

.p-value__item-text {
  align-self: stretch;
  font-size: 1.8rem;
  line-height: 1.6;
  letter-spacing: 0.1em;

  @include mq(max, tab) {
    font-size: 1.6rem;
  }

  li {
    position: relative;
    padding-left: 2.4rem;

    &::before {
      content: "";
      position: absolute;
      top: 0.7em;
      left: 0;
      width: 0.9rem;
      height: 0.9rem;
      background-color: var(--color-beige);
    }

    &+li {
      margin-top: 3.5rem;
    }
  }
}

// ============= stay =============
.p-stay {
  padding-block: 14rem;
  background: var(--color-white01);

  @include mq(max, tab) {
    padding-block: 6rem 8rem;
  }

  .l-wrapper {
    overflow: hidden;
  }

  .c-title {
    margin-bottom: 5rem;

    @include mq(max, tab) {
      margin-bottom: 7rem;
    }
  }
}

.p-stay__group {
  &+.p-stay__group {
    margin-top: 12rem;

    @include mq(max, tab) {
      margin-top: 6rem;
    }
  }
}

.p-stay__group-title {
  position: relative;
  display: inline-block;
  margin-bottom: 6rem;
  padding-right: 3.5rem;
  padding-bottom: 1.5rem;
  font-size: 2.5rem;
  font-weight: 500;
  line-height: 1.6;
  letter-spacing: 0.1em;

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    right: 0;
    left: -100vw;
    height: 1px;
    background-color: var(--color-sand);
  }

  @include mq(max, tab) {
    padding-bottom: 2rem;
    margin-bottom: 5rem;
    font-size: 2rem;
    line-height: 1;
  }

  small {
    font-size: 1.6rem;
    font-weight: 500;

    @include mq(max, tab) {
      font-size: 1.4rem;
    }
  }
}

.p-stay__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 8rem;
  padding-inline: 5.1rem;

  @include mq(max, tab) {
    grid-template-columns: 1fr;
    gap: 5rem;
    padding-inline: 0;
  }
}

.p-stay__item-image {
  margin-bottom: 4rem;

  img {
    width: 100%;
    height: auto;
  }
}

.p-stay__item-time {
  margin-bottom: 2rem;
  display: block;
  font-size: 1.8rem;
  font-weight: 500;
  letter-spacing: 0.1em;
}

.p-stay__item-title {
  margin-bottom: 3rem;
  font-size: 2.5rem;
  font-weight: 500;
  line-height: 1.6;
  letter-spacing: 0.1em;

  @include mq(max, tab) {
    font-size: 2.1rem;
  }
}

.p-stay__item-text {
  font-weight: 500;
  line-height: 1.9;
  letter-spacing: 0.1em;

  @include mq(max, tab) {
    font-size: 1.4rem;
  }

  small {
    font-size: 1.4rem;
  }
}

// ============= service =============
.p-service {
  padding-block: 14rem;
  background: var(--color-off-white);

  @include mq(max, tab) {
    padding-block: 6rem 0;
  }

  .c-title {
    margin-bottom: 5rem;

    @include mq(max, tab) {
      margin-bottom: 3.2rem;
    }
  }
}

.p-service__lead {
  margin-bottom: 8rem;
  font-size: 1.8rem;
  font-weight: 500;
  line-height: 1.6;
  letter-spacing: 0.1em;

  @include mq(max, tab) {
    margin-bottom: 6rem;
    font-size: 1.6rem;
  }
}

.p-service__list {
  display: flex;
  flex-direction: column;
  gap: 8rem 0;

  @include mq(max, tab) {
    gap: 6rem 0;
  }
}

.p-service__item {
  position: relative;
  display: flex;
  justify-content: space-between;
  padding-top: 10rem;

  @include mq(max, tab) {
    flex-direction: column-reverse;
    padding-top: 0;
  }

  &:nth-child(even) {
    flex-direction: row-reverse;

    @include mq(max, tab) {
      flex-direction: column-reverse;
    }
  }
}

.p-service__item-body {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 512px;
  height: 49.4rem;
  padding-inline: 7.1rem;
  background: var(--color-white01);

  @include mq(max, tab-xxl) {
    padding-inline: 4rem;
  }

  @include mq(max, tab) {
    width: unset;
    height: auto;
    align-self: stretch;
    margin-inline: -2rem;
    padding-inline: 4rem;
    padding-block: 4rem 5rem;
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 100%;
    width: 38.48%;
    background: var(--color-white01);


    @include mq(max, tab) {
      top: auto;
      bottom: 100%;
      left: 0;
      right: 0;
      width: auto;
      height: 5rem;
    }
  }

  .p-service__item:nth-child(even) & {
    margin-left: auto;
    align-items: flex-end;

    @include mq(max, tab) {
      margin-inline: -2rem; // margin-left: autoを上書き
      align-items: stretch;
    }

    &::after {
      left: auto;
      right: 100%;
    }
  }
}

.p-service__item-title {
  margin-bottom: 3rem;
  font-size: 3rem;
  font-weight: 500;
  letter-spacing: 0.1em;

  // 偶数：タイトルは全幅を維持してテキストと頭を揃える
  .p-service__item:nth-child(even) & {
    align-self: stretch;
  }

  @include mq(max, tab) {
    margin-bottom: 2.5rem;
    font-size: 2.5rem;
  }
}

.p-service__item-text {
  font-weight: 500;
  line-height: 1.9;
  letter-spacing: 0.1em;

  @include mq(max, tab) {
    font-size: 1.4rem;
  }

  small {
    font-size: 1.4rem;

    @include mq(max, tab) {
      font-size: 1.2rem;
    }
  }
}

.p-service__item-image {
  position: relative;
  z-index: 1;
  width: 50%;
  margin-top: -13rem;
  align-self: flex-start;
  overflow: hidden;

  @include mq(max, tab) {
    width: 100%;
    margin-top: 0;
  }

  img {
    width: 100%;
    height: auto;
  }
}

// ============= room =============
.p-room {
  padding-block: 14rem;
  background: var(--color-white01);

  @include mq(max, tab) {
    padding-block: 6rem 10rem;
    background: var(--color-off-white);
  }

  .c-title {
    margin-bottom: 5rem;

    @include mq(max, tab) {
      margin-bottom: 3.2rem;
    }
  }
}

.p-room__lead {
  margin-bottom: 8rem;
  font-size: 1.8rem;
  font-weight: 500;
  line-height: 1.9;
  letter-spacing: 0.1em;
  text-align: center;

  @include mq(max, tab) {
    margin-bottom: 6rem;
    font-size: 1.4rem;
    line-height: 1.6;
    text-align: left;
  }
}

.p-room__item {
  &+.p-room__item {
    margin-top: 10rem;

    @include mq(max, tab) {
      margin-top: 6rem;
    }
  }

  // ▼ SP時：display: contents でフラット化し order で並び替え
  @include mq(max, tab) {
    display: flex;
    flex-direction: column;
  }
}

.p-room__item-visual {
  display: flex;
  justify-content: space-between;
  gap: 0 8rem;
  margin-bottom: 4rem;

  @include mq(max, tab) {
    display: contents;
  }
}

.p-room__item-gallery {
  max-width: 78.5rem;
  flex: 1;

  @include mq(max, tab) {
    display: contents;
  }
}

.p-room__item-gallery-main {
  margin-bottom: 1rem;

  @include mq(max, tab) {
    order: 1;
    margin-inline: -2rem;
    margin-bottom: .53rem;
  }

  img {
    width: 100%;
    height: auto;
  }
}

.p-room__item-gallery-thumbs {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 0 1.11rem;
  margin-bottom: 4rem;

  @include mq(max, tab) {
    order: 2;
    gap: 0 .589rem;
    margin-inline: -2rem;
    margin-bottom: 4.5rem;
  }

  img {
    width: 100%;
    height: auto;
  }
}


.p-room__item-plan {
  flex-shrink: 0;

  @include mq(max, tab) {
    order: 4;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0;
  }

  img {
    display: block;
    height: auto;

    &+img {
      margin-top: 1.9rem;

      @include mq(max, tab) {
        margin-top: 0;
      }
    }
  }
}

.p-room__item-plan-note {
  margin-top: 2rem;
  font-size: 1.4rem;
  font-weight: 500;
  line-height: 1.9;
  letter-spacing: 0.1em;
  text-align: right;

  @include mq(max, tab) {
    text-align: center;
  }
}

.p-room__item-body {
  @include mq(max, tab) {
    order: 3;
    margin-bottom: 4.5rem;
  }
}

.p-room__item-name {
  margin-bottom: 2.5rem;
  font-size: 2.5rem;
  font-weight: 500;
  line-height: 1.6;
  letter-spacing: 0.1em;

  span {
    margin-left: 2rem;
    font-size: 2rem;

    @include mq(max, tab) {
      display: block;
      margin-left: 0;
      font-size: 1.8rem;
    }
  }

  // 補足情報（洋室7帖/専有面積 等）
  small {
    font-size: 1.6rem;
    font-weight: 500;

    @include mq(max, tab) {
      display: block;
      font-size: 1.4rem;
    }
  }
}

.p-room__item-text {
  font-weight: 500;
  line-height: 1.9;
  letter-spacing: 0.1em;

  @include mq(max, tab) {
    font-size: 1.4rem;
  }
}

// ============= facility =============
.p-facility {
  padding-block: 14rem;
  background: var(--color-off-white);

  @include mq(max, tab) {
    padding-block: 10rem 6rem;
  }

  .c-title {
    margin-bottom: 8rem;

    @include mq(max, tab) {
      margin-bottom: 6rem;
    }
  }
}

.p-facility__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 6.5rem 3.5rem;
  margin-bottom: 8rem;

  @include mq(max, tab) {
    grid-template-columns: 1fr;
    gap: 3.5rem 0;
    margin-bottom: 6rem;
  }
}

.p-facility__item {
  display: flex;
  flex-direction: column;
}

.p-facility__item-image {
  margin-bottom: 3rem;

  @include mq(max, tab) {
    margin-bottom: 2rem;
  }

  img {
    width: 100%;
    height: auto;
  }
}

.p-facility__item-name {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  flex: 1;
  font-weight: 500;
  line-height: 1.6;
  letter-spacing: 0.1em;

  small {
    font-size: 1.4rem;
  }
}

.p-facility__detail {
  padding-block: 8rem;
  padding-inline: 12.7rem;
  background: var(--color-white01);

  @include mq(max, tab) {
    padding-block: 3rem 6rem;
    padding-inline: 3rem;
  }
}

.p-facility__section {
  &:not(:last-child) {
    margin-bottom: 12rem;

    @include mq(max, tab) {
      margin-bottom: 7rem;
    }
  }
}

.p-facility__section-title {
  display: inline-flex;
  align-items: center;
  gap: 0 0.5rem;
  margin-bottom: 6rem;
  font-size: 2rem;
  font-weight: 500;
  line-height: 1.6;
  letter-spacing: 0.1em;

  @include mq(max, tab) {
    margin-bottom: 3rem;
    font-size: 1.6rem;
  }
}

.p-facility__bracket {
  font-size: 3.2rem;
  font-weight: 400;
  line-height: 1;

  @include mq(max, tab) {
    font-size: 2.5rem;
  }
}

.p-facility__group {
  &:not(:last-child) {
    margin-bottom: 8rem;

    @include mq(max, tab) {
      margin-bottom: 5rem;
    }
  }
}

.p-facility__group-title {
  display: flex;
  align-items: center;
  gap: 0 1.5rem;
  margin-bottom: 4rem;
  font-size: 2rem;
  font-weight: 500;
  line-height: 1.6;
  letter-spacing: 0.1em;

  @include mq(max, tab) {
    font-size: 1.6rem;
  }

  // ▼ テキスト右端まで伸びる線（#4D4D4D / 30%）
  &::after {
    content: "";
    flex: 1;
    height: 1px;
    background: var(--color-dark-gray);
    opacity: 0.3;
  }
}

.p-facility__check-list {
  display: grid;
  grid-template-columns: 0.6fr 1fr;
  gap: 3rem 1rem;

  @include mq(max, tab) {
    grid-template-columns: 1fr;
    gap: 3rem 0;
  }

  &--single {
    grid-template-columns: 1fr;
  }
}

.p-facility__check-item {
  display: flex;
  align-items: baseline;
  gap: 0 1.2rem;
  font-size: 1.6rem;
  font-weight: 500;
  line-height: 1.6;
  letter-spacing: 0.1em;

  &::before {
    content: "";
    flex-shrink: 0;
    width: 1.1rem;
    height: 1.1rem;
    background: var(--color-warm-gray);
  }

  small {
    font-size: 1.4rem;
  }

  &--wide {
    grid-column: 1 / -1;
  }

  a {
    color: #0000FF;
    transition: opacity 0.7s;

    &:hover {
      opacity: 0.3;
    }
  }
}

// ============= price =============
.p-price {
  padding-top: 14rem;
  background: var(--color-white01);

  @include mq(max, tab) {
    padding-top: 6rem;
  }

  .c-title {
    margin-bottom: 5rem;
  }
}

.p-price__banner {
  position: relative;
  max-width: 76.8rem;
  margin-inline: auto;
  padding-block: 4.5rem 5.6rem;
  margin-bottom: 10rem;
  background: var(--color-light-sand);
  text-align: center;

  @include mq(max, tab) {
    padding-block: 3.3rem 4.7rem;
    margin-bottom: 6rem;
  }

  // ▼ 内側の白い装飾線
  &::before {
    content: "";
    position: absolute;
    top: 1.3rem;
    bottom: 1.3rem;
    left: 1.1rem;
    right: 1.1rem;
    border: 1px solid var(--color-white01);
    pointer-events: none;

    @include mq(max, tab) {
      left: 1.4rem;
      right: 1.4rem;
    }
  }
}

.p-price__banner-label {
  padding-block: .4rem;
  margin-bottom: 2.5rem;
  background: var(--color-white01);
  font-size: 2rem;
  font-weight: 500;
  line-height: 1.6;
  letter-spacing: 0.1em;
  color: var(--color-muted-gray);

  @include mq(max, tab) {
    padding-block: 1rem;
    margin-bottom: 3rem;
    font-size: 1.6rem;
    line-height: 1;
  }
}

.p-price__banner-main {
  font-size: 3rem;
  font-weight: 500;
  line-height: 1.9;
  letter-spacing: 0.1em;

  @include mq(max, tab) {
    margin-bottom: 2rem;
    font-size: 2.3rem;
    line-height: 1.2;
  }

  // ▼ 金額を大きく強調
  strong {
    font-size: 4rem;
    font-weight: 500;

    @include mq(max, tab) {
      font-size: 3rem;
    }
  }
}

.p-price__banner-sub {
  margin-bottom: 3rem;
  font-size: 1.9rem;
  font-weight: 500;
  line-height: 1.9;
  letter-spacing: 0.1em;

  @include mq(max, tab) {
    font-size: 1.4rem;
    line-height: 1.1;
  }
}

.p-price__banner-example {
  font-size: 1.4rem;
  font-weight: 500;
  line-height: 1.3;
  letter-spacing: 0.1em;

  @include mq(max, tab) {
    margin-bottom: 1.3rem;
  }
}

.p-price__banner-calc {
  font-size: 1.4rem;
  font-weight: 500;
  line-height: 1.3;
  letter-spacing: 0.1em;

  @include mq(max, tab) {
    line-height: 1.4;
  }

  // ▼ 「通常 390,000円」部分（SPのみサイズ変更）
  &-price {
    @include mq(max, tab) {
      font-size: 1.4rem;
    }
  }

  // ▼ 「（65,000円×6泊）」部分（SPのみサイズ変更）
  &-note {
    @include mq(max, tab) {
