.p-faq__icon {
  position: relative;
  flex-shrink: 0;
  width: 2.25rem;
  height: 2.25rem;

  // 横線（常時表示）
  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    background: var(--color-dark-gray);
    transition: transform 0.3s ease;
  }

  // 横線
  &::before {
    width: 2.25rem;
    height: 1px;
    transform: translate(-50%, -50%);
  }

  // 縦線（開いた時に回転して消える）
  &::after {
    width: 1px;
    height: 2.25rem;
    transform: translate(-50%, -50%);
  }

  // 開いた状態：縦線を回転して横線と重ねる
  .p-faq__question[aria-expanded="true"] & {
    &::after {
      transform: translate(-50%, -50%) rotate(90deg);
    }
  }
}

.p-faq__answer {
  display: grid;
  grid-template-rows: 0fr;
  transition: grid-template-rows 0.3s ease;

  // 開いた状態
  &[aria-hidden="false"] {
    grid-template-rows: 1fr;
  }
}

