@charset "utf-8";
@use "../global" as *;

// PCでだけ改行する（SPでは非表示）
.u-br-pc {
  display: inline;

  @include mq(max, note-m) {
    display: none;
  }
}

// SPでだけ改行する（PCでは非表示）
.u-br-sp {
  display: none;

  @include mq(max, note-m) {
    display: inline;
  }
}

// PCでだけ改行する（tab以下で非表示）
.u-br-pc--tab {
  display: inline;

  @include mq(max, tab) {
    display: none;
  }
}

// SPでだけ改行する（tab以下で表示）
.u-br-sp--tab {
  display: none;

  @include mq(max, tab) {
    display: inline;
  }
}