@charset "utf-8";
@use '../global' as *;

.l-header {
  position: fixed;
  top: 2.3rem;
  left: 0;
  z-index: 100;
  width: 100%;
  height: 4.5rem;
  opacity: 0;

  @include mq(max, tab) {
    height: 3.8rem;
    top: 2.1rem;
  }
}

.l-header__container {
  height: inherit;
  display: flex;
  justify-content: space-between;
  align-items: center;
  // max-width: 1280px;
  padding-inline: 4rem 3rem;
  margin-inline: auto;

  @include mq(max, tab) {
    padding-inline: 2rem;
  }
}

.l-header__logo {

  a {
    display: block;
    transition: opacity .3s;

    &:hover {
      opacity: .7;
    }
  }

  img {
    @include mq(max, tab) {
      width: 11.527rem;
      height: 3.522rem;
    }
  }
}

.l-header__inner {
  position: relative;
  z-index: 2;
  height: inherit;
  display: flex;
  align-items: center;
  gap: 0 3.2rem;

  @include mq(max, tab) {
    gap: 0 2rem;
  }
}

// ---- ナビゲーション（開閉メニュー） ----
.l-header__nav {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 1;
  width: 45%;
  width: 55.6rem;
  height: 100vh;
  background: rgba(255, 255, 255, 0.9);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s, visibility 0.3s;

  @include mq(max, tab) {
    width: 100%;
    height: 100vh;
  }

  // 開いた状態
  .l-header.is-nav-open & {
    opacity: 1;
    visibility: visible;
  }
}

.l-header__nav-list {
  display: grid;
  grid-template-columns: auto auto;
  place-content: center;
  height: 100%;
  gap: 3rem 4rem;
  font-weight: 500;
  line-height: 1.6;
  letter-spacing: 0.1em;
  color: var(--color-dark-gray);

  a {
    transition: opacity 0.3s;

    &:hover {
      opacity: 0.7;
    }
  }
}

.l-header__reserve {
  height: inherit;
  display: grid;
  place-items: center;
  width: 22rem;
  background: transparent;
  border: .1rem solid var(--color-dark-gray);
  font-size: 1.8rem;
  line-height: 1.6;
  letter-spacing: 0.1em;
  color: var(--color-dark-gray);
  transition: opacity 0.3s;

  &:hover {
    opacity: 0.7;
  }

  @include mq(max, tab) {
    width: 12rem;
    font-size: 1rem;
  }
}