@charset "utf-8";
@use '../global' as *;

.c-title {}

.c-title__en {
  // margin-bottom: 10rem;
  display: block;
  font-family: var(--ff-unna);
  font-size: 16.516rem;
  // line-height: 1.6;
  letter-spacing: 0.1em;
  color: var(--color-beige);
  // @include line-height(1.9);

  @include mq(max, tab) {
    margin-bottom: 3rem;
    font-size: 9rem;
    @include line-height(1.4);
  }
}

.c-title__ja {
  position: relative;
  display: block;
  margin-left: 12.8rem;
  padding-left: 2.9rem;
  font-size: 2.5rem;
  font-weight: 500;
  line-height: 1.6;
  letter-spacing: 0.1em;
  color: var(--color-dark-gray);

  @include mq(max, tab) {
    margin-left: 2rem;
    font-size: 1.8rem;
  }

  small {
    font-size: 2rem;
    font-weight: 500;
    line-height: 1;

    @include mq(max, tab) {
      font-size: 1.6rem;
    }
  }

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    display: block;
    width: 1.85rem;
    height: 1px;
    background: var(--color-dark-gray);
  }
}

.c-title--center {
  text-align: center;

  @include mq(max, tab) {
    text-align: left;
  }

  .c-title__ja {
    margin-left: 0;
    padding-left: 0;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include mq(max, tab) {
      margin-left: 2rem;
      padding-left: 2.9rem;
      display: block;
    }

    &::before {
      position: static;
      transform: none;
      margin-bottom: 3rem;

      @include mq(max, tab) {
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
        margin-bottom: 0;
      }
    }
  }
}