@charset "utf-8";
@use '../global' as *;

.c-hamburger {
  position: relative;
  width: 3.6rem;
  height: 4.3rem;
  background: none;
  border: none;
  cursor: pointer;

  @include mq(max, tab) {
    width: 2.8rem;
    height: 3.8rem;
  }

  // 共通の線スタイル
  &>span {
    position: absolute;
    left: 0;
    display: block;
    width: 100%;
    height: 1px;
    background-color: var(--color-dark-gray);
    transition: transform 0.3s, top 0.3s, bottom 0.3s;

    @include mq(max, tab) {
      width: 2.8rem;
    }

    // 上の線：上から 15.7px
    &:first-child {
      top: 1.57rem;

      @include mq(max, tab) {
        top: 1.5rem;
      }
    }

    // 下の線：下から 15.7px
    &:last-child {
      bottom: 1.57rem;

      @include mq(max, tab) {
        bottom: 1.5rem;
      }
    }
  }

  // ---- 開いた状態（×に変形） ----
  .l-header.is-nav-open &>span {
    &:first-child {
      top: 50%;
      transform: rotate(45deg);
    }

    &:last-child {
      bottom: auto;
      top: 50%;
      transform: rotate(-45deg);
    }
  }
}