@charset "UTF-8";
@use '../setting' as *;

// ブレイクポイント
// -----------------------------------------
@use "sass:map"; // ← これがポイント！ map.get を使うため

// ブレイクポイント定義
$breakpoints: (
  // --- 既存：スマホ周り ---
  sm-s: 370px,
  sm: 450px,
  sm-l: 500px,

  // ★追加：370〜500の間をもう少し刻む（小型〜標準スマホの微調整）
  sm-xs: 320px,
  // かなり小さい端末向け（SEなど想定）
  sm-m: 390px,
  // iPhone 12/13/14あたりの基準にしやすい
  sm-ml: 430px,
  // 450より少し手前で調整したい時用
  sm-xl: 540px,
  // 500の次（ちょい大きめスマホ〜小型タブ手前）

  // --- 既存：タブレット周り ---
  tab-s: 600px,

  // ★追加：600〜768の間（小タブ/横持ちスマホなど）
  tab-xs: 640px,
  tab-sm: 700px,

  tab: 768px,
  tab-l: 810px,
  tab-xl: 850px,

  // ★追加：850〜980の間（大きめタブレット〜小ノート手前）
  tab-xxl: 900px,
  tab-3xl: 940px,

  note-s: 980px,
  ipad-6: 1024px,

  // ★追加：1024〜1180の間（タブ横〜ノート入口の刻み）
  note-xs: 1100px,
  ipad-8: 1120px,
  // iPad系の中間として使いやすい
  note-sm: 1140px,

  ipad-10: 1180px,
  note: 1200px,

  // ★追加：1200〜1280の間（コンテンツ幅の微調整用）
  note-m: 1240px,

  note-l: 1280px,

  // ★追加：1280〜1440の間（PCレイアウトの段階切替）
  note-lg: 1366px,
  // よくあるノート解像度の基準に便利
  note-xlg: 1400px,

  note-xl: 1440px,

  // ★追加：1440〜1600の間（ワイド対応の刻み）
  note-xxxl: 1536px,
  // よくあるワイド基準
  note-4k-s: 1560px,

  note-xxl: 1600px,

  // ★追加：1600以上（超ワイド）
  wide: 1720px,
  wide-l: 1920px,
  ) !default;


// 使い方：@include mq(min, tab) {...} / @include mq(max, note) {...} / @include mq(bw, tab, note) {...}
@mixin mq($type, $bp1, $bp2: null) {

  // キー存在チェック（安全）
  @if not map.has-key($breakpoints, $bp1) {
    @error "mq(): undefined breakpoint `#{$bp1}`.";
  }

  @if ($type ==bw) and (not $bp2 or not map.has-key($breakpoints, $bp2)) {
    @error "mq(bw): need 2 valid breakpoints, got `#{$bp1}`, `#{$bp2}`.";
  }

  @if ($type ==min) {

    // min-width: >= bp1
    @media screen and (min-width: #{map.get($breakpoints, $bp1)}) {
      @content;
    }

  }

  @else if ($type ==max) {
    // max-width は "未満"にしたいことが多いので 1px 引く
    $w: map.get($breakpoints, $bp1) - 1px;

    @media screen and (max-width: #{$w}) {
      @content;
    }

  }

  @else if ($type ==bw) {
    // between: bp1 以上 〜 bp2 未満
    $min: map.get($breakpoints, $bp1
    );
  $max: map.get($breakpoints, $bp2) - 1px;

  @media screen and (min-width: #{$min}) and (max-width: #{$max}) {
    @content;
  }

}

@else {
  @error "mq(): type must be `min`, `max`, or `bw`. got `#{$type}`.";
}
}


/* ブレイクポイントの使い方
** @include mq(min, md) { }
** @include mq(max, md) { }
** @include mq(bw,sm, md) {}
*/