@charset "utf-8";
@use '../global' as *;

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

// Lenis 用スタイル
html.lenis,
html.lenis body {
  height: auto;
}

.lenis.lenis-smooth {
  scroll-behavior: auto;
}

.lenis.lenis-stopped {
  overflow: hidden;
}

body {
  // width: min(100%, 1280px);
  margin-inline: auto;
  background: var(--color-off-white);
  font-size: 1.6rem;
  font-family: var(--ff-shipporiMincho);
  font-weight: 400;
  line-height: 1;
  word-break: break-word;
  line-break: strict;
  font-feature-settings: "palt";
  color: var(--color-dark-gray);

  &.is-menu-open {
    overflow: hidden;
  }

  ul,
  li {
    padding: 0;
    margin: 0;
    list-style: none;
  }

  a {
    text-decoration: none;
    cursor: pointer;
  }

  img,
  video {
    max-width: 100%;
    vertical-align: bottom;
  }

  input {
    appearance: revert;
  }
}