@charset "utf-8";
@use '../global' as *;

.l-footer {
  padding-block: 10rem 9rem;
  background: var(--color-cream);

  @include mq(max, tab) {
    padding-block: 6rem 7rem;
  }
}

.l-footer__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0 4rem;

  @include mq(max, tab) {
    flex-direction: column;
    gap: 6rem 0;
  }
}

.l-footer__logo {
  margin-bottom: 3rem;

  @include mq(max, tab) {
    margin-bottom: 3rem;
    text-align: center;
  }

  a {
    display: block;
    transition: opacity 0.3s;

    &:hover {
      opacity: 0.7;
    }
  }

  img {
    height: auto;
  }
}

.l-footer__company {
  font-size: 1.2rem;
  font-weight: 500;
  line-height: 1.8;
  letter-spacing: 0.1em;

  @include mq(max, tab) {
    text-align: center;
  }
}

.l-footer__nav {
  padding-right: 5rem;

  @include mq(max, tab) {
    padding-right: 0;
  }
}

.l-footer__nav-list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2.4rem 8rem;
  font-weight: 500;
  line-height: 1.6;
  letter-spacing: 0.1em;

  @include mq(max, tab) {
    gap: 2.4rem 3rem;
    font-size: 1.4rem;
  }

  a {
    color: inherit;
    transition: opacity 0.3s;

    &:hover {
      opacity: 0.7;
    }
  }
}